import { Formik, Field } from 'formik';
import {
	Button,
	CardActions,
	CardContent,
	CircularProgress,
	Divider,
	FormHelperText,
	Grid,
	MenuItem,
	Select,
	Stack,
	TextField,
} from '@material-ui/core';
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';
import { makeStyles } from '@material-ui/core/styles';
import { gql, useMutation } from '@apollo/client';
import SubCard from '../../../../ui-component/cards/SubCard';
import SearchProperty from './SearchProperty';
import InputLabel from '../../../../ui-component/extended/Form/InputLabel';
import NumberFormatCustom from '../../components/NumberFormatCustom';
import { PropertyData, TEditProperty } from '../../../../types';
import TaxReport from './TaxReport';
import UploadFile from '../../components/UploadFile';
import { addPreviewProp } from '../../../../helpers/file';
import AnimateButton from '../../../../ui-component/extended/AnimateButton';
import { isApproved } from '../Rules';
import { useSnackBar, useUploadFiles } from '../../../../hooks';
import { getPropertyThumbnailPutUrl } from '../../../../graphql/queries';
import { validationEditSchema } from '../Validation';
import graphqlSchema from '../../../../graphql';
import { updatePropertyCache } from '../../../../dataProvider/cache';
import ListFee from './ListFee';
import SellerInfo from './SellerInfo';
import PropertyInformation from './PropertyInformation';

type Props = {
	formData: Object;
	vestingTypesData: Array<string>;
	onSelectProperty: (property: PropertyData) => void;
};

const useStyles = makeStyles(theme => ({
	container: {
		paddingTop: theme.spacing(1),
	},
	downloadLink: {
		marginRight: theme.spacing(2),
	},
}));

const Form = (props: Props) => {
	const classes = useStyles();
	const showSnackBar = useSnackBar();
	const { formData, vestingTypesData, onSelectProperty } = props;

	const property = _get(formData, 'getProperty', {});
	const {
		PropertyId: propertyId,
		DTPropertyId: dTPropertyId = '',
		PropertyData: propertyData,
		SellerInfo: sellerInfo,
		StatusId: statusId,
		VestingTypeMapped: vestingTypeMapped,
		ReportStatus: taxReportStatus,
		BuyerAgentFeePct: buyerAgentFeePct,
		ListingAgentFeePct: listingAgentFeePct,
	} = property;

	const listPriceRange = _get(property, 'ListPriceRange', {});
	const priceMax = _get(listPriceRange, 'Max', 0);
	const priceMin = _get(listPriceRange, 'Min', 0);

	const presignedUrl = _get(property, ['PropertyReport', 'PresignedUrl'], '');
	const yearBuilt =
		_get(propertyData, 'YearBuilt', '') ||
		_get(propertyData, 'YearBuiltOverride', '');
	const vestingTypeTmp =
		_get(propertyData, 'VestingTypeMapped', false) ||
		_get(propertyData, 'VestingTypeOverride', false);
	const vestingType = vestingTypesData.includes(vestingTypeTmp)
		? vestingTypeTmp
		: 'Unknown';
	const approved = isApproved(statusId);
	const isDisableYearBuilt = approved || !_isEmpty(yearBuilt);
	const isDisableVestingType = approved || !_isEmpty(vestingTypeMapped);
	const isShowDataPropertyData = !_isEmpty(dTPropertyId && propertyData);

	const [updatePropertyDataAction, { loading: isSaving }] = useMutation(
		gql(graphqlSchema.updatePropertyData),
		{
			update: updatePropertyCache(propertyId),
			onCompleted: () => {
				showSnackBar({ message: 'Property updated', alertSeverity: 'success' });
			},
			onError: e => {
				const message = _get(e, 'message', 'Something went wrong');
				showSnackBar({ message });
			},
		}
	);

	const { files, onFilesChange, uploadFiles, isUploadLoading } = useUploadFiles({
		name: 'getPropertyThumbnailPutUrl',
		query: getPropertyThumbnailPutUrl,
		generateVariables: files => ({ Photos: files, PropertyId: propertyId }),
	});

	const onSubmitHandler = (values: TEditProperty) => {
		const {
			vestingType,
			yearBuilt,
			priceMax,
			priceMin,
			buyerAgentFeePct,
			listingAgentFeePct,
		} = values;
		uploadFiles();
		const variables = {
			PropertyId: propertyId,
			...(!isDisableVestingType && {
				VestingType: { VestingType: vestingType },
			}),
			...(!isDisableYearBuilt && { YearBuilt: { YearBuilt: yearBuilt } }),
			ListPriceRange: { Max: priceMax, Min: priceMin },
			...(approved && { ListingAgentFeePct: listingAgentFeePct }),
			...(approved && { BuyerAgentFeePct: buyerAgentFeePct }),
		};
		updatePropertyDataAction({
			variables,
		});
	};

	const initialValues = {
		dataTreePropertyId: dTPropertyId,
		vestingType,
		priceMin,
		priceMax,
		yearBuilt,
		buyerAgentFeePct,
		listingAgentFeePct,
	};

	return (
		<Formik
			initialValues={initialValues}
			onSubmit={onSubmitHandler}
			enableReinitialize
			validationSchema={validationEditSchema}
		>
			{({ errors, handleChange, handleBlur, handleSubmit, touched, values }) => (
				<form noValidate onSubmit={handleSubmit}>
					<CardContent>
						<Grid container spacing={2} alignItems='center'>
							<Grid item lg={6} xs={12}>
								<SellerInfo sellerInfo={sellerInfo} />
								<br />
								{isShowDataPropertyData && (
									<Grid item>
										<SubCard title='Property Photo'>
											<UploadFile
												name='thumbnail'
												files={files}
												maxFiles={1}
												onDrop={files => onFilesChange(addPreviewProp(files))}
											/>
										</SubCard>
									</Grid>
								)}
								<br />
								<ListFee isShow={approved} />
							</Grid>
							<Grid item lg={6} xs={12}>
								<Grid item>
									<SubCard
										title={
											isShowDataPropertyData ? 'Property Information' : 'Add Property'
										}
									>
										<Grid item>
											<SearchProperty onSelect={onSelectProperty} disabled={approved} />
										</Grid>
										{isShowDataPropertyData && (
											<Grid container className={classes.container} spacing={2}>
												<Grid item lg={6} xs={12}>
													<InputLabel>List Price Min</InputLabel>
													<TextField
														id='priceMin'
														value={values.priceMin}
														fullWidth
														placeholder='List Price Min'
														onChange={handleChange}
														onBlur={handleBlur}
														error={touched.priceMin && Boolean(errors.priceMin)}
														helperText={touched.priceMin && errors.priceMin}
														name='priceMin'
														InputProps={{
															inputComponent: NumberFormatCustom as any,
														}}
													/>
												</Grid>
												<Grid item lg={6} xs={12}>
													<InputLabel>List Price Max</InputLabel>
													<TextField
														id='priceMax'
														value={values.priceMax}
														fullWidth
														placeholder='List Price Max'
														onChange={handleChange}
														onBlur={handleBlur}
														error={touched.priceMax && Boolean(errors.priceMax)}
														helperText={touched.priceMax && errors.priceMax}
														name='priceMax'
														InputProps={{
															inputComponent: NumberFormatCustom as any,
														}}
													/>
												</Grid>
											</Grid>
										)}
										{isShowDataPropertyData && (
											<Grid item xs={12} lg={12}>
												<Grid container spacing={2} className={classes.container}>
													<Grid item lg={6} xs={12}>
														<InputLabel>Built Year</InputLabel>
														<TextField
															id='yearBuilt'
															value={values.yearBuilt}
															fullWidth
															placeholder='Built Year'
															onChange={handleChange}
															onBlur={handleBlur}
															error={touched.priceMin && Boolean(errors.yearBuilt)}
															helperText={touched.priceMin && errors.yearBuilt}
															name='yearBuilt'
															disabled={isDisableYearBuilt}
														/>
													</Grid>
													<Grid item lg={6} xs={12}>
														<InputLabel>Vesting Type</InputLabel>
														<Select
															defaultValue='Unknown'
															labelId='vesting-select'
															id='vestingType'
															name='vestingType'
															value={values.vestingType}
															onChange={handleChange}
															disabled={isDisableVestingType}
														>
															<MenuItem value='Unknown'>
																<em>Unknown</em>
															</MenuItem>
															{!_isEmpty(vestingTypesData) &&
																vestingTypesData.map((type: string) => (
																	<MenuItem key={type} value={type}>
																		{type}
																	</MenuItem>
																))}
														</Select>
													</Grid>
												</Grid>
											</Grid>
										)}
										{isShowDataPropertyData && (
											<PropertyInformation property={property} />
										)}
										{!isShowDataPropertyData && (
											<TaxReport taxReportStatus={taxReportStatus} />
										)}
										<Field
											type='hidden'
											name='dataTreePropertyId'
											value={values.dataTreePropertyId || ''}
											onChange={handleChange}
											onBlur={handleBlur}
											id='dataTreePropertyId'
										/>
										{touched.dataTreePropertyId && errors.dataTreePropertyId && (
											<FormHelperText error>{errors.dataTreePropertyId}</FormHelperText>
										)}
									</SubCard>
								</Grid>
							</Grid>
						</Grid>
					</CardContent>
					<Divider />
					<CardActions>
						<Grid item xs={12}>
							<Stack direction='row' justifyContent='flex-end'>
								<AnimateButton>
									{!_isEmpty(presignedUrl) && (
										<Button
											type='button'
											variant='outlined'
											color='secondary'
											href={presignedUrl}
											className={classes.downloadLink}
										>
											Export Property Data
										</Button>
									)}
									<Button
										color='primary'
										variant='contained'
										type='submit'
										disabled={isUploadLoading || isSaving}
									>
										{isUploadLoading || isSaving ? (
											<CircularProgress size='1.5rem' color='secondary' />
										) : (
											'Save'
										)}
									</Button>
								</AnimateButton>
							</Stack>
						</Grid>
					</CardActions>
				</form>
			)}
		</Formik>
	);
};

export default Form;
